.about {
  color: $color-blue-gray;

  background: $color-blue;
  background-image: url("../img/about-bg.png");
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: contain;

  img {
    display: block;
  }

  p {
    margin: 0;
  }
}

.about__wrapper {
  display: flex;
  justify-content: space-between;

  margin: 0;
  padding: 60px 45px 72px;
}

.about__description {
  margin: 0;
  max-width: 350px;
}

.about__title {
  position: relative;

  margin: 0;
  margin-bottom: 14px;
  padding-left: 117px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $white;

  &:before {
    content: "";
    position: absolute;
    left: 0;
    top: 16px;

    width: 100px;
    height: 16px;

    background: url("../img/icon__decor-heading.svg") no-repeat center center;
  }
}

.about__text {
  line-height: $baseLineHeght - 2;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.about__info {
  display: flex;
  justify-content: center;
  align-items: baseline;

  margin: 0;
  margin-top: 32px;
  margin-left: 4px;
  padding: 0;

  line-height: 20px;

  li {
    margin-left: 4%;
    width: 25%;

    text-align: center;
    list-style: none;
  }

  b {
    display: block;

    margin-bottom: 12px;
    width: 153px;
    height: 153px;

    font-size: $largFontSize + 12;
    line-height: 153px;
    color: $white;

    border: 2px solid $color-blue-light;
    border-radius: 50%;
  }
}

@media (min-width: $media-breakpoint-sm) {
  margin: 0;
}
