.intro {
  min-height: 582px;
  min-height: 585px;

  background-image: url("../img/bg.jpg");
  background-position: top center;
  background-repeat: no-repeat;
  background-size: contain;

  img {
    display: block;
  }
}

.intro__wrapper {
  padding: 45px;
}

.intro__title {
  margin: 0;
  padding-top: 181px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largerFontSize;
  line-height: $largerLineHeght;
  color: $color-blue;
}

.intro__text {
  display: block;
  margin-bottom: 38px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $color-gray;
}

.intro__link {
  margin-top: 21px;
}

@media (min-width: $media-breakpoint-sm) {
  margin: 0;
}

@media (min-width: $media-breakpoint-lg) {
  margin: 0;
}

@media (min-width: $media-breakpoint-xl) {
  margin: 0;
}
