.news {
  margin: 0;
}

.news__wrapper {
  margin: 0;
  padding: 92px 45px 140px;
}

.news__head {
  display: flex;
  justify-content: space-between;

  margin-bottom: 70px;
}

.news__title {
  position: relative;

  margin: 0;
  padding-left: 117px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $color-darker-gray;

  &:before {
    content: "";
    position: absolute;
    left: 0;
    top: 16px;

    width: 100px;
    height: 16px;

    background: url("../img/icon__decor-heading-blue.svg") no-repeat center center;
  }
}

.news__body {
  display: flex;
  justify-content: space-between;
}

.news__own {
  display: flex;
  flex-basis: 100%;

  .news__link {
    display: block;
  }

  img {
    margin-right: 30px;
  }
}

.news__own-body {
  display: flex;
  flex-direction: column;

  max-width: 254px;

  .news__link {
    margin-top: auto;
  }
}

.news__own-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.news__head-link {
  font-size: $baseFontSize - 2;
  line-height: $baseLineHeght + 1;
  letter-spacing: 0.05em;
  color: $color-darker-gray;
}

.news__own-text {
  margin-top: 46px;
}

.news__date {
  font-size: $baseFontSize - 6;
  line-height: $baseLineHeght - 7;
  color: $color-gray;
}

.news__text {
  color: $color-gray;
}

.news__link {
  color: $color-gray;
}

.news__list {
  display: flex;

  margin: 0;
  padding: 0;

  list-style: none;

  li {
    margin-left: 42px;
    width: 50%;
  }

  img {
    display: block;
  }

  p {
    margin: 0;
  }

  .news__head-link {
    display: inline-block;

    min-height: 46px;
  }

  .news__text {
    margin: 23px 0 32px;;
  }
}

.news__item-body {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  .news__date {
    margin-left: 16px;
  }
}

@media (min-width: $media-breakpoint-sm) {
  margin: 0;
}
