.button {
  display: block;

  padding: 9px 10px;
  max-width: 145px;

  font-size: $baseFontSize;
  line-height: $baseLineHeght;
  text-align: center;
  color: $white;

  background-color: $color-blue;
  border: 1px solid $color-blue;
  border-radius: 102px;


  &--white {
    color: $color-blue;

    background-color: $white;
  }

  &--big {
    max-width: 188px;
    padding: 10px 26px;

    font-size: $baseFontSize + 4;
    line-height: $baseLineHeght + 8;
  }
}
