.projects {
  color: $color-dark-gray;

  background: $color-gray-light;
}

.projects__wrapper {
  margin: 0;
  padding: 60px 45px 50px;
}

.projects__title {
  position: relative;

  margin: 0;
  margin-bottom: 23px;
  padding-left: 117px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $color-darker-gray;

  &:before {
    content: "";
    position: absolute;
    left: 0;
    top: 16px;

    width: 100px;
    height: 16px;

    background: url("../img/icon__decor-heading-blue.svg") no-repeat center center;
  }
}

.projects__list {
  display: flex;
  flex-wrap: wrap;

  margin: 0;
  padding: 0;

  font-size: $baseFontSize + 4;
  line-height: $baseLineHeght + 6;

  list-style: none;

  li {
    margin-bottom: 28px;
    margin-right: 30px;

    width: 350px;

    background-color: $white;

    &:nth-child(3n) {
      margin-right: 0;
    }
  }

  img {
    display: block;
  }
}

.projects__text {
  display: flex;
  justify-content: center;
  flex-direction: column;

  margin: 0;
  padding: 8px 18px;
  height: 94px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $baseFontSize + 4;
  line-height: $baseLineHeght;
  text-align: center;

  span {
    display: block;

    font-size: $baseFontSize;
  }
}

@media (min-width: $media-breakpoint-sm) {
  margin: 0;
}
