.page-header {
  padding: 0;
}

.page-header__top {
  display: flex;
  align-items: center;

  margin: 12px 45px;
}

.page-header__logo {
  margin-right: 20px;
}

.page-header__slogan {
  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $baseFontSize - 2;
  line-height: 23px;
  color: $color-dark-gray;
}

.page-header__contacts {
  margin-left: auto;

  font-size: $baseFontSize - 4;
  line-height: 17px;
  text-decoration: none;
  color: $color-blue;

  img {
    vertical-align: middle;
    margin-right: 4px;
  }
}

.page-header__bottom {
  padding: 0 45px;

  height: 70px;

  background-color: $color-blue;
}

.page-header__bottom-wrapper {
  display: flex;
  align-items: center;
  height: 100%;
}

.page-header__social {
  display: flex;

  margin: 0;
  padding: 0;

  list-style: none;

  li:not(:first-child) {
    margin-left: 14px;
  }

  img {
    display: block;
  }
}

.page-header__navbar-toggler {
  margin: 0;
}

@media (min-width: 1200px) {
  .page-header__top,
  .page-header__bottom-wrapper {
    margin-right: auto;
    margin-left: auto;
    width: 1110px;
  }
}
