.events {
  margin: 0;

  background-color: $color-blue;
}

.events__wrapper {
  margin: 0;
  padding: 136px 45px 140px;
}

.events__head {
  display: flex;
  justify-content: space-between;

  margin-bottom: 60px;
}

.events__title {
  position: relative;

  margin: 0;
  padding-left: 117px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $white;

  &:before {
    content: "";
    position: absolute;
    left: 0;
    top: 16px;

    width: 100px;
    height: 16px;

    background: url("../img/icon__decor-heading.svg") no-repeat center center;
  }
}

.events__controls {
  margin-top: -5px;
  margin-right: -8px;
}

.events__arrow {
  background-color: transparent;
  border: 0;

  cursor: pointer;
}

.events__list {
  margin: 0;

  font-size: $baseFontSize;
  line-height: $baseLineHeght + 3;
  color: $color-darker-gray;
}

.events__item {
  position: relative;

  width: 540px;

  margin-right: 30px;

  img {
    display: block;
  }

  p {
    position: absolute;
    right: 36px;
    bottom: 36px;

    margin: 0;
    padding: 16px 25px;
    max-width: 200px;

    background-color: $white;
  }
}

@media (min-width: $media-breakpoint-sm) {
  margin: 0;
}
