.page-footer {
  margin: 0;
  padding: 0;

  font-size: $baseFontSize - 6;
  line-height: $baseLineHeght - 7;
  color: $white;

  background-color: $color-blue;

  a {
    color: $white;
  }
}

.page-footer__wrapper {
  padding: 25px 45px;
}

.page-footer__top{
  display: flex;
  justify-content: space-between;
  padding-bottom: 35px;

  border-bottom: 1px solid $white;
}

.page-footer__phone {
  font-size: $baseFontSize - 4;
  line-height: $baseLineHeght - 5;
}

.page-footer__middle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 0;

  border-bottom: 1px solid $white;

  a {
    font-size: inherit;
    line-height: inherit;
  }
}

.page-footer__nav {
  flex-wrap: wrap;
  max-width: 520px;

  li {
    margin-right: 100px;

    &:first-child {
      margin-bottom: 16px;
    }
  }
}

.page-footer__social {
  display: flex;

  margin: 0;
  padding: 0;

  list-style: none;

  li:not(:first-child) {
    margin-left: 14px;
  }
}

.page-footer__bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 25px;
}

@media (min-width: $media-breakpoint-lg) {

}
